import './App.css';
import Particlesbg from './components/Particlesbg.js';
import anime from 'animejs';
import "@babylonjs/core/Debug/debugLayer";
import "@babylonjs/inspector";
import "@babylonjs/loaders/glTF";
import { Engine, Scene, ArcRotateCamera, Vector3, HemisphericLight, Mesh, MeshBuilder } from "@babylonjs/core";
import sustav from './videos/sustav.mp4';
import vichna1 from './images/vichna1.jpeg';
import vichna2 from './images/vichna2.png'
import vichna3 from './images/vichna3.png'
import vichna4 from './images/vichna4.png'
import prot from './images/prot.png';
import de from './images/de.png'

function App() {
  return (
    <div class="App">

      {/* -----------------НАЧАЛНА СТРАНИЦА----------------- */}
      <div class="blurPart">
        <Particlesbg></Particlesbg>
      </div>
      <div class="homeDiv">
        <h1><b>БЕЛТЪЦИ</b></h1>
        <h2>От Йоан Добчев, Владимир Томашевич и Явор Петров</h2>
        <button class="btn" onClick={() => {
          anime({
            targets: ".homeDiv",
            translateY: -(window.innerHeight) / 2 - 100,
            duration: 2000,
            opacity: 0,
            easing: "easeInOutElastic(1, .7)"
          });
          setTimeout(function () {

            document.getElementById("beltuciMain").style.display = "block";
            document.querySelector(".homeDiv").style.display = "none";

            anime({
              targets: "#beltuciMain",
              opacity: ['0%', '100%'],
              duration: 500,
              easing: "linear",
            })
          }
            , 2500);
        }}>Презентирай</button>
      </div>
      {/* -----------------БЕЛТЪЦИ----------------- */}
      <div id="beltuciMain">
        <h1>Какво са белтъците?</h1>
        <div id="beltuci" class="cardtext">
          <h1 id="title1" class="cardtext">Белтъци</h1>
          <div class="descText cardtext">
            <h2>Белтъците са едни от основните компоненти на живите клетки. Влиза в цитроплазмата, клетъчнато ядро, костите и хрущялите, кожата и много други. Съставят
            около 20% от човешкия организъм. Изпълняват защитни (активна и пасивна), транспортни и съкратителни до каталитични и регулаторни функции. Белтъците са изградени от 20 вида аминокиселини. Тези аминокиселини са изградени от вълглероден атом свързван с карбоксилна група COOH и аминогрупа NH2. Останалата част от молекулата се нарича аминокиселинен остатък.</h2>
          </div>
          <button class="btn" id="beltuciBut" onClick={contBut}>Продължи</button>
        </div>

        <div class="Sp" id="protImg">
          <img src={prot} width="500px" height="500px"></img>
        </div>

      </div>

      {/* ---------------------------------------------- */}
      <div id="stroez1" class="stroez">
        <div id="infoSp1" class="infoSp bx1">
          <h2 id="title"></h2>
          <p id="text"></p>
          <button onClick={nextSlide} class="btn" id="but1">Продължи</button>
        </div>
        <div id="Sp1" class="Sp Sp1">
          <video width="710px" height="400px" autoPlay loop>
            <source src={sustav}></source>
          </video>
        </div>
      </div>
      <div id="stroez2" class="stroez">
        <div id="infoSp2" class="infoSp bx2">
          <h2 id="title2"></h2>
          <p id="text2"></p>
          <button onClick={nextSlide} class="btn" id="but1">Продължи</button>
        </div>
        <div id="Sp2" class="Sp Sp2">
          <img src={vichna1}></img>
        </div>
      </div>
      <div id="stroez3" class="stroez">
        <div id="infoSp3" class="infoSp bx3">
          <h2 id="title3"></h2>
          <p id="text3"></p>
          <button onClick={nextSlide} class="btn" id="but1">Продължи</button>
        </div>
        <div id="Sp3" class="Sp Sp3">
          <img src={vichna2}></img>
        </div>
      </div>
      <div id="stroez4" class="stroez">
        <div id="infoSp4" class="infoSp bx4">
          <h2 id="title4"></h2>
          <p id="text4"></p>
          <button onClick={nextSlide} class="btn" id="but1">Продължи</button>
        </div>
        <div id="Sp4" class="Sp Sp4">
          <img src={vichna3}></img>
        </div>
      </div>
      <div id="stroez5" class="stroez">
        <div id="infoSp5" class="infoSp bx5">
          <h2 id="title5"></h2>
          <p id="text5"></p>
          <button onClick={nextSlide} class="btn" id="but1">Продължи</button>
        </div>
        <div id="Sp5" class="Sp Sp5">
          <img src={vichna4}></img>
        </div>
      </div>

      <div id="stroez6" class="stroez">
        <div id="infoSp6" class="infoSp bx6">
          <h1>Свойства</h1>
          <ul>
            <li>
              <b>Денатурация</b> - При въздействие върху белтъците на
              g-радиация, UV, висока температура, силни киселини, силни основи, органични
              разтворители – III-чната и IV-чната структура на белтъците се
              разрушава (разрушават се слабите връзки, които стабилизират тези
              нива на организация)
            </li>
            <li>
              <b>Разтворимост</b> - различна във вода и разтвори на електролити
              – дължи се на хидратационната обвивка; Глобуларните се разтварят
              по-добре във вода от фибриларните
            </li>
            <li>
              <b>Полиелектролити</b> – във водни разтвори белтъците носят
              определен електричен заряд.Зареждането на белтъците с електричен
              заряд, специфичен за всеки белтък при определено рН се използва в
              метода за разделяне на белтъците – ЕЛЕКТРОФОРЕЗА
            </li>
            <li>
              <b>Ренатурация</b> – при определени условия (бавно въвеждане, а
              след това отстраняване) на денатуриращия агент – е възможно
              нативната струкура на белтъчната молекула да се възстанови.
            </li>
          </ul>
          <button class="btn" onClick={nextSlide}>ПРОДЪЛЖИ</button>
        </div>
        <div class="Sp Sp6">
          <h1>Процес на денатурация</h1>
          <img src={de} width="400px" height="250px" ></img>
        </div>
      </div>


      <div id="stroez7" class="stroez">
        <div>
          <h1>Благодаря за вниманието!</h1>
        </div>
      </div>

    </div>
  );
}
var slide = 1;
function animTxt(txts, ids, speed) {
  var i = 0, j = 0;
  function ti() {
    if (i < txts[0].length) {
      document.getElementById(ids[0]).innerHTML += txts[0].charAt(i);
      i++;
      setTimeout(ti, speed);
    }
  }
  ti();
  function txt() {
    if (j < txts[1].length) {
      document.getElementById(ids[1]).innerHTML += txts[1].charAt(j);
      j++;
      setTimeout(txt, speed);
    }
  }
  txt();
}
function nextSlide() {
  anime({
    targets: `.bx${slide}`,
    duration: 1500,
    translateX: -2000,
    easing: 'spring(1, 80, 10, 0)'
  })
  anime({
    targets: `.Sp${slide}`,
    duration: 1500,
    translateX: 2000,
    easing: 'spring(1, 80, 10, 0)'
  })
  setTimeout(() => {
    document.getElementById(`stroez${slide}`).style.display = "inline";
    anime({
      targets: `#stroez${slide}`,
      opacity: 1,
      duration: 2500,
    });
    switch (slide) {
      case 2:
        animTxt(["Първична структура", "Първичната структура на белтъка се определя от вида и подреждането на алфа-аминокиселините остатъци. Притежава линейна форма. Информацията за структурата се записва в молекулите на ДНК и се унаследява."], ["title2", "text2"], 3)
        break;
      case 3:
        animTxt(["Вторична структура", "Вторичната структура на белтъците е когато полипептидната верига се нагъне поради взаимодействието на несъвместими аминокиселини. Структурата става спираловидна или като нагънат лист. Придава се еластичност на белтъка. Тази структура е трайна поради многото водородни връзки който се образуват."], ["title3", "text3"], 3)
        break;
      case 4:
        animTxt(["Третична структура", "Извън вторичната структура могат да се образува гънки за образуването на коваленти връзки. Това образува третичната структура."], ["title4", "text4"], 3)
        break;
      case 5:
        animTxt(["Четвъртична структура", "Четвъртина структура се образува при свързването на две или повече еднакви или различни полипептидни вериги. Те се свързват с помощта на водородни и ковалентни връзки."], ["title5", "text5"], 3)
        break;
    }
  }, 3000);
  slide++;
}
//--------------------------------------------------
function contBut() {
  // anime({
  //   targets: ".descText",
  //   opacity: 0,
  //   duration: 3000
  // })

  setTimeout(() => {
    anime({
      targets: "#beltuciMain",
      translateX: -5000,
      duration: 2000,
      easing: 'cubicBezier(.5, .05, .1, .3)'
    })
  }, 500);

  setTimeout(() => {
    document.getElementById("stroez1").style.display = "inline";
    anime({
      targets: "#stroez1",
      opacity: 1,
      duration: 2500,
    })
    animTxt(["Състав", "Изследван е състава на белтъците и той показва, че те се състоят главно от въглерод, водород, кислород, азот и сяра. Молекулната им маса варира от 5000 до няколко милиона. Белтъците са биополимери."], ["title", "text"], 3)
  }, 3000)
}
export default App;

